import { NgModule } from '@angular/core';
import { Routes, RouterModule, CanActivate } from '@angular/router';

import { PageNotFoundComponent } from './components/layout/page-not-found/page-not-found.component';
import { LoginComponent } from './components/account/login/login.component';
import { HomeComponent } from './components/home/home.component';
import { LayoutComponent } from './components/layout/layout.component';
import { RegisterComponent } from './components/account/register/register.component';
import { ResetPasswordComponent } from './components/account/reset-password/reset-password.component';
import { ProfileComponent } from './components/account/profile/profile.component';
import { ShoppingListsComponent } from './components/account/shopping-lists/shopping-lists.component';
import { ContactComponent } from './components/home/contact/contact.component';
import { AboutComponent } from './components/home/about/about.component';
import { DetailsComponent } from './components/home/stores/details/details.component';
import { HelpComponent } from './components/home/help/help.component';
import { FaqComponent } from './components/home/faq/faq.component';
import { CareersComponent } from './components/home/careers/careers.component';
import { TermsComponent } from './components/home/terms/terms.component';
import { VendorSignupComponent } from './components/account/vendor-signup/vendor-signup.component';
import { ChangePasswordComponent } from "./components/account/change-password/change-password.component";

import { AuthGuard } from './services/auth-service/auth.guard';
import { PrivacyComponent } from './components/home/privacy/privacy.component';
import { VendorTermsComponent } from './components/home/vendor-terms/vendor-terms.component';

/// added
import { NstoresComponent } from './components/nstores/nstores.component'
import { NproductsComponent } from './components/nproducts/nproducts.component'


const routes: Routes = [
  {
    path: '',
    component: LayoutComponent,
    children: [
      { path: '', component: HomeComponent },
      /// added
      { path: 'stores', component: NstoresComponent },
      { path: 'products', component: NproductsComponent },
      { path: 'about', component: AboutComponent },
      { path: 'contact', component: ContactComponent },
      { path: 'store/:guid', component: DetailsComponent, canActivate: [AuthGuard] },
      { path: 'stores/details', component: DetailsComponent, canActivate: [AuthGuard] },
      { path: 'account', component: ProfileComponent, canActivate: [AuthGuard] },
      { path: 'account/login', component: LoginComponent },
      { path: 'account/profile', component: ProfileComponent, canActivate: [AuthGuard] },
      { path: 'account/reset-password', component: ResetPasswordComponent },
      { path: 'account/register', component: RegisterComponent },
      { path: 'account/shopping-lists', component: ShoppingListsComponent, canActivate: [AuthGuard] },
      { path: 'account/become-a-vendor', component: VendorSignupComponent },
      { path: 'account/change-password', component: ChangePasswordComponent },
      { path: 'careers', component: CareersComponent },
      { path: 'help', component: HelpComponent },
      { path: 'faq', component: FaqComponent },
      { path: 'terms', component: TermsComponent },
      { path: 'privacy', component: PrivacyComponent },
      { path: 'vendor-terms-conditions', component: VendorTermsComponent },
      { path: '**', component: PageNotFoundComponent },
      
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload' })],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule {
}
