  @Input() selling_price: string;
  @Input() barcode: string;

  constructor() { }

  ngOnInit(): void {

  }

  generatePrice(): any {
    if (this.selling_price) {
      var high = this.selling_price.split('.')[0],
        low = this.selling_price.split('.')[1];

      // return `<span>R${high}</span>.<span>${low}</span>`;
      return 'R' + this.selling_price;
    }

  }

