  headers = new HttpHeaders().set('Content-Type', 'application/json')



  // check the handle error
  // this is the check the error when result of api
  // there is the client or server side error detect part
  handleError(error: HttpErrorResponse) {
    let msg = '';
    console.log("----- this is the error -----");
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = `client server : ${error.error.message}`;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }

  postApi(url, data): Observable<any> {
    return this.http.post(url, data, {headers: this.headers, responseType: 'text'}).pipe(
      catchError(this.handleError)
    )
  }

  getApi(url): Observable<any> {
    return this.http.get(url,{ headers: this.headers, responseType: 'text'}).pipe(
      catchError(this.handleError)
    )
  }

