import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  nameFocused = '';
  lastnameFocused = '';
  emailFocused = '';
  numberFocused = '';
  messageFocused = '';

  constructor() { }

  ngOnInit(): void {
   
  }

  contactUs(): void {
  }

  inputFocused(value:string, focusIn:boolean, el:string) {
    if (focusIn) {
      this[el] = 'focused';
    } else {
      if (value.length) {
        this[el] = 'focused';
      } else {
        this[el] = '';
      }
    }
  }

}
